.CardElement{
  
    color:black;
}
.CardHeader{
   
    color:var(--primary-text);
    font-weight: 900;

}
.Card{
    background-color: var(--card-bg);
    box-shadow: 3px 3px 3px 3px var(--card-box);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    color:var(--primary-text);
    margin-top: 100px;
    width: min(500px,45vw);
    display: inline-block;
    margin-bottom: 20px;
    opacity: 0.8;
    transition: 0.15s;
    box-sizing: border-box;
 
}
.CardImage{
    min-height: 200px;
    width: min(400px,40vw);
    padding: 20px;
}
.Card:hover{
    transform: scale(1.1);
    box-shadow:4px 4px 4px 4px var(--card-hover);
    background-color:  var(--card-hover);
}
.Card a{
    overflow: wrap;
}
@media (max-width:600px)
{
    .Card{
        margin-left: 10px;
        margin-right: 10px;
        width: 95%;
    }
}
.card-content{
    font-weight: bold;
    color:black;
}
.Card a{
    color:var(--primary-color);
}
