.Footer{
    color:var(--primary-color);
    background-color:rgb(15, 15, 15,0.7);
    box-shadow: 3px 3px 3px 3px var(--card-box);
    border-radius: 5px;
    padding: 20px;
    margin: 50px;
    margin:auto;
    margin-top: 100px;
    width: min(500px,80vw);
    bottom:0;
}
.center{
    display:flex;
    justify-content:center;
}
.Footer a{
    transition: 0.15s;
    color:red;
    padding: 10px;
}
.Footer ul{
    margin:0;
    padding:0;
}
.Footer li{
    padding:0;
    margin:0;
}
.Footer a:hover{
    transform: scale(1.2);
}
a.social {
    margin: 0 1rem;
    transition: transform 100ms;
    display: inline-block;
  }
.fa-icon{
    color:red !important;
}
a.social:hover {
    transform: translateY(-5px);
  }
  @media (max-width:600px) {
      .Footer{
          width: 80%;
      }
      .Footer a{
        font-size: 7px;
      }
      
  }